/**
 * Shortcodes (Positioning)
 ============================================================================ */
.center-element {
  margin-left: auto;
  margin-right: auto;
}

@media all and (max-width: 60em) {
  .pull-right,
  [dir="rtl"] .pull-left {
    float: right;
  }

  .pull-left,
  [dir="rtl"] .pull-right {
    float: left;
  }
}
